import React, { Component } from 'react'
import './startForm.css'
class StartForm extends Component  {
constructor() {
super();
this.state = {
name: "React",
type: ""
};
this.handleButton = this.handleButton.bind(this);
//this.hideComponent = this.hideComponent.bind(this);
}
handleButton(e){
  this.setState({
    type:e.target.id,
  }, function () {

      this.props.hideComponent("startForm", this.state);

    }.bind(this));
  e.preventDefault();  
}
render(){
return (
<div>
	<div class="container" id="start_menu" >
		<div class="row justify-content-center">
		 
		
		  
		
			  <div class="col-md-6 col-12">
			  <div class="card  shadow-sm zoom" id="bookcard">
				<div class="card-body">
					<a href="" className="a" id="booking" onClick={this.handleButton}><img  id="booking" className="icon_button  mx-auto" src="https://lockdoctor.ie/images/book.png" alt="" width="250" height="250"/>
					<h1 class="card-title pricing-card-title">Make a Booking</h1>
          </a>
				</div>
			</div>
			  </div>

			  <span className="or"></span>

			    <div class="col-md-6 col-12">
				<div class="card  shadow-sm zoom" id="quotecard">
				<div class="card-body">
					<a href="" className="a"  id="quote" onClick={this.handleButton}><img id ="quote" className="icon_button  mx-auto" src="https://lockdoctor.ie/images/quote.png" alt="" width="250" height="250"/>
					<h1 id="quote" class="card-title pricing-card-title">Get a Quote</h1>
          </a>
				</div>
			</div>
				</div>
			

			
		
		
		</div>
	</div>
</div>
)
}
}
export default StartForm